<template>

 	<div id="map" class="mx-auto border-2 border-black shadow-xl shadow-black rounded m-4"></div>
</template>

<script>
import L from 'leaflet'
import axios from 'axios'
export default {
   name: 'Map',
   props:[],
   data() {
    return {
      hosts:[],
    }
   },
   mounted() {
   	var map = L.map('map',{
      center:[38.995583773578616,3.2623649993881014],
      zoom:4
    })
    L.tileLayer('https://api.maptiler.com/maps/streets-v2/{z}/{x}/{y}.png?key=PD8XJ66HVcmOugKVcgUz', {
    attribution: '<a href="https://www.maptiler.com/copyright/" target="_blank">&copy; MapTiler</a> <a href="https://www.openstreetmap.org/copyright" target="_blank">&copy; OpenStreetMap contributors</a>'
	}).addTo(map)
	//add mark to the map
    var markerGroup = L.layerGroup().addTo(map);

    axios.get("map").then((response)=>{
      const arr = response.data
      arr.forEach((element)=>{

        L.marker(element.coor).addTo(markerGroup).bindPopup(`<div class="font-bold text-black text-center">${element.price}</div>`,{autoClose: false, autoPan: false}).openPopup()

      })
    })
    

}

}

</script>

<style>
#map {
  width:97%;
  height:80vh;
}
</style>